export default {
    components:{
    },
    data(){
        return{
            tokenPacks: [{
                coin: 10,
                price: 150000,
                save:  0,
                should:false
            }, {
                coin: 25,
                price: 300000,
                save:  20,
                should:false
            }, {
                coin: 70,
                price: 735000,
                save:  30,
                should: true
            }, {
                coin: 150,
                price: 1350000,
                save:  40,
                should:false
            }, {
                coin: 400,
                price: 3000000,
                save:  50,
                should:false
            }],
            payWays: [{
                icon: 'card',
                text: 'Thẻ quốc tế',
                subText: 'Ngân hàng nước ngoài phát hành'
            }, {
                icon: 'vn_card',
                text: 'Thẻ quốc tế',
                subText: 'Ngân hàng Việt Nam phát hành'
            }, {
                icon: 'zalo',
                text: 'Thanh toán qua ZaloPay',
                subText: ''
            }, {
                icon: 'momo',
                text: 'Thanh toán qua MOMO',
                subText: ''
            }],
            objSelect:{},
            activeCombo:null
        }
    },
    mounted(){
        this.activePackage(2);
    },
    methods:{
        payment(){
           this.loader()
           this.$post(`member/coin`,this.objSelect)
                .then(res => {
                   this.$auth.fetchUser()
                   this.loader(0)
                   this.$notify({ group: 'all', text: 'Thanh toán thành công',  type: 'dark'})
                })
                .catch(err => {
                    this.loader(0)
                    console.log(err)
                })
        },
        activePackage(idx){
            // this.tokenPacks.forEach(pack => pack.should = false)
            // this.tokenPacks[idx].should = true
            this.activeCombo = idx
            this.objSelect = {
                coin: this.tokenPacks[idx].coin,
                price: this.tokenPacks[idx].price
            }
        },
    }
}
